.note-popover {
    display: none !important;
}

.note-editor {
    border: 0 !important;
    .note-toolbar {
        &::after {
            clear: both;
            content: "";
            display: block;
        }
    }
    .note-editable {
        background: #f9f9f9;
        display: block;
        p {
            font-size: 16px;
        }
        ul {
            list-style-type: disc;
            margin-left: 2em;
        }
        table {
            td {
                border: 1px solid #ccc;
                width: auto;
            }
        }
    }
    .modal {
        display: none;
    }
    .btn-group {
        float: left;
        &.note-tools, {
            float: right;
        }
    }
    .btn {
        background: #f9f9f9;
        border: 0;
        border-radius: 4px;
        color: #666;
        cursor: pointer;
        margin-right: 2px;
        padding-top: 4px;
        &.disabled {
            background: transparent;
            color: #aaa;
            .merge-tags {
                background: #f9f9f9;
                color: #666;
            }
        }
    }
}
.tooltip {
